<template>
  <div>
    <div class="nav_title"></div>
    <el-card>
      <el-table
      :data="courses"
      :default-sort="{prop:'term',order:'descending'}">
        <el-table-column prop="term" label="" align="center" width="120" sortable></el-table-column>
        <el-table-column prop="coursecode" label="" align="center" width="140"></el-table-column>
        <el-table-column prop="title" label="" align="center" show-overflow-tooltip></el-table-column>
        <el-table-column prop="credits" label="" align="center" width="120"></el-table-column>
        <el-table-column label="" align="center" width="80">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.coursetype==1"></el-tag>
            <el-tag v-else-if="scope.row.coursetype==2" type="success"></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" align="center" width="150">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.campus==1" type="success"></el-tag>
            <el-tag v-else-if="scope.row.campus==2"></el-tag>
            <el-tag v-else-if="scope.row.campus==3" type="warning"></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="info" label="" align="center"></el-table-column>
        <el-table-column prop="capacity" label="" align="center" width="100"></el-table-column>
        <el-table-column prop="selectednumber" label="" align="center" width="100"></el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
    name:"MyTeach",
    data() {
      return {
        userInfo:{},
        courses:[],
      }
    },
    methods:{
      getMyTeach(){
        this.$api.course.getTeach(this.userInfo.username)
        .then(res=>{
          this.courses = res.data["data"]['courses'];
        })
      }
    },
    created(){
      this.$api.userInfo.getUserInfo(sessionStorage.getItem("username"))
      .then(res=>{
        this.userInfo = res.data["data"]["user"];
        this.getMyTeach();
      })
    }
}
</script>

