import http from '../utils/http'
import qs from 'qs'

// 
export function getGrades(username){
    return http({
        method:"GET",
        url:`getGrades/?username=${username}`,
    })
}

// 
export function tryGetGrade(cid,sid){
    return http({
        method:"GET",
        url:`tryGetGrade/?cid=${cid}&sid=${sid}`,
    })
}

// 
export function writeGrade(data={}){
    return http({
        method:"POST",
        url:"writeGrade/",
        data:qs.stringify(data),
    })
}