<template>
  <div>
    <el-card>
      <div slot="header" class="clearfix">
        <div class="nav_title"></div>
      </div>
      <el-collapse v-model="activeNames" class="myinfo_all">
        <el-collapse-item name="1" class="info">
          <template slot="title">
            <span class="spantitle"></span>
          </template>
          <div><el-tag type="success" size="small">{{userInfo.fullname}}</el-tag></div>
          <div><el-tag type="success" size="small">{{userInfo.username}}</el-tag></div>
          <div>{{userInfo.identity}}</div>
          <div>{{major.title}}</div>
        </el-collapse-item>
        <el-collapse-item name="2" class="info">
          <template slot="title">
            <span class="spantitle"></span>
          </template>
          <div><el-tag size="small">{{college.title}}</el-tag></div>
          <div>{{college.majorcode}}</div>
          <div><el-tag size="small">{{major.title}}</el-tag></div>
          <div>{{major.majorcode}}</div>
          <div>{{userInfo.term}} - {{userInfo.stage}}</div>
        </el-collapse-item>
        <el-collapse-item name="3">
          <template slot="title">
            <span class="spantitle"></span>
          </template>
          <div>
            <el-button type="text" @click="dialogFormVisible = true"></el-button>
            <el-dialog title="" :visible.sync="dialogFormVisible">
              <el-form :model="form" :rules="rules" ref="changeFormRef">
                <el-form-item label="" label-width="120px">
                  {{userInfo.username}}
                </el-form-item>
                <el-form-item label="" label-width="120px" prop="oldpwd">
                  <el-input v-model="form.oldpwd" auto-complete="off" style="width:500px"></el-input>
                </el-form-item>
                <el-form-item label="" label-width="120px" prop="newpwd">
                  <el-input v-model="form.newpwd" auto-complete="off" style="width:500px" minlength="5" maxlength="30"></el-input>
                </el-form-item>
              </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button @click="cancelChange"> </el-button>
                <el-button type="primary" @click="changePassword"> </el-button>
              </div>
            </el-dialog>
          </div>
        </el-collapse-item>
      </el-collapse>
    </el-card>
  </div>
</template>

<script>
export default {
    name:"MyInfo",
    data() {
      return {
        activeNames:['1'],
        userInfo:{},
        major:{},
        college:{},
        dialogFormVisible: false,
        form: {
          oldpwd: '',
          newpwd: '',
        },
        // 
        rules: {
            oldpwd: [
                { required: true, message: '', trigger: 'blur' },
                { min: 5, max: 30, message: ' 5  30 ', trigger: 'blur' }
            ],
            newpwd: [
                { required: true, message: '', trigger: 'blur' },
                { min: 5, max: 30, message: ' 5  30 ', trigger: 'blur' }
            ],
        }
      }
    },
    methods:{
      cancelChange(){
        this.dialogFormVisible = false ;
        // this.$message({message: ''});
        this.$notify({
          title: '',
          message: "",
          type: 'info',
          offset:100
        });
      },
      changePassword(){
        this.$refs.changeFormRef.validate(valid=>{
            // 
            if(!valid) return;
            let data = {
              oldpwd:this.form.oldpwd,
              newpwd:this.form.newpwd,
            }
            this.$api.userInfo.changePassword(sessionStorage.getItem("username"),data)
            .then((res)=>{
              // this.$message({
              //   message:res.data["msg"],
              //   type:"success",
              // });
              this.$notify({
                title: '',
                message: res.data["msg"],
                type: 'success',
                offset:100
              });
              this.dialogFormVisible = false;
            })
            .catch((err)=>{
              this.$message({
                  message:err.response.data["msg"],
                  type:"error",
                });
              // this.$notify({
              //   title: '',
              //   message: err.response.data["msg"],
              //   type: 'error',
              //   offset:100
              // });
            });
        });
      }
    },
    mounted() {
      this.$api.userInfo.getUserInfo(sessionStorage.getItem("username"))
      .then(res=>{
        this.userInfo = res.data["data"]["user"];
        this.major = res.data["data"]["major"];
        let idtf = sessionStorage.getItem("identity");
        if(idtf==1)
          this.userInfo.identity = "";
        else if(idtf==2)
          this.userInfo.identity = "";
        else if(idtf==3)
          this.userInfo.identity = "";
        // 
        this.$api.userInfo.getCollege(this.major.pcode)
        .then(res=>{
          this.college = res.data["data"]["college"];
        })
        .catch(()=>{
          this.college = this.major;
        });
      })
    }
}
</script>

<style scoped>
  .info div{
    height: 30px;
  }
  .myinfo_all div{
    font-size: 15px;
    color: #606266;
  }

</style>