<template>
  <div>
    <div class="nav_title"></div>
    <el-form :inline="true" :model="askFrom" class="demo-form-inline">
      <el-form-item label="">
        <el-input v-model="askFrom.term" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="askFrom.campus" placeholder="">
          <el-option label="" value="9"></el-option>
          <el-option label="" value="1"></el-option>
          <el-option label="" value="2"></el-option>
          <el-option label="" value="0"></el-option>
          <el-option label="" value="3"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="searchCourse"></el-button>
      </el-form-item>
    </el-form>
    <el-card class="box-card">
      <el-table stripe :data="showCourse">
        <el-table-column prop="coursecode" label="" align="center" width="150"></el-table-column>
        <el-table-column prop="title" label="" align="center" width="200"></el-table-column>
        <el-table-column prop="teacher.major.title" label="" align="center" width="120"></el-table-column>
        <el-table-column prop="teacher.fullname" label="" align="center" width="120"></el-table-column>
        <el-table-column prop="credits" label="" align="center" width="80"></el-table-column>
        <el-table-column label="" align="center" width="120">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.campus==1" type="success"></el-tag>
            <el-tag v-else-if="scope.row.campus==2"></el-tag>
            <el-tag v-else-if="scope.row.campus==0" type="warning"></el-tag>
            <el-tag v-else-if="scope.row.campus==3" type="info"></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="info" label="" align="center"></el-table-column>
        <el-table-column label="" align="center" width="80">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.coursetype==1"></el-tag>
            <el-tag v-else-if="scope.row.coursetype==2" type="success"></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="capacity" label="" align="center" width="80"></el-table-column>
        <el-table-column prop="selectednumber" label="" align="center" width="80"></el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[5,10,20]"
        :page-size="pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="allCourse.length"
        style="margin:20px 0;float:right;">
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
    name:"SchoolCourse",
    data() {
      return {
        allCourse:[],
        askFrom:{
          term:1,
          campus:"9",
        },
        currentPage:1,
        pagesize:10,
        showCourse:[],
      }
    },
    methods: {
      changeShowCourse(){
        this.showCourse = this.allCourse.slice((this.currentPage-1)*this.pagesize,this.currentPage*this.pagesize);
      },
      handleSizeChange(val){
        this.pagesize = val;
        this.changeShowCourse();
      },
      handleCurrentChange(val){
        this.currentPage = val;
        this.changeShowCourse();
      },
      searchCourse(){
        this.$api.course.getAllCourse(this.askFrom.term,this.askFrom.campus)
        .then(res=>{
          this.allCourse = res.data["data"]["courses"];
          this.currentPage = 1;
          // this.$message({
          //   message:"",
          //   type:"success",
          // });
          this.$notify({
            title: '',
            message: '',
            type: 'success',
            offset:100
          });
          this.changeShowCourse();
        })
        .catch(err=>{
          console.log(err)
          // this.$message({
          //   message:"",
          //   type:"error",
          // })
          this.$notify({
            title: '',
            message: err.data.response["msg"],
            type: 'error',
            offset:100
          });
        })
      }
    },
}
</script>

<style scoped>

</style>