<template>
  <div>
    <!--  -->
    <el-card>
        <el-row>
        <el-col :span="2"><div style="font-size:25px;">{{userInfo.fullname}}</div></el-col>
        <el-col :span="16">
          <div style="font-size:18px; display:flex; justify-content:space-around;">
            <span>{{userInfo.gender==0?"":""}}</span> 
            <span>{{userInfo.username}}</span>
            <span>{{college.title}}</span>
            <span>{{userInfo.major.title}}</span>
          </div>
        </el-col>
        <el-col :span="3" :offset="3">
          <el-button @click="closeSelect"></el-button>
        </el-col>
        </el-row>
    </el-card>
    <br>
    <!--  -->
    <el-row>
      <!--  -->
      <el-col :span="10">
        <el-card>
          <el-row>
            <el-col :span="12"><div style="font-size:18px; font-weight:bold; margin-bottom:20px;"></div></el-col>
            <el-col :span="12">
              <div style="display:flex; justify-content:flex-end;">
                <el-button @click="addCourseCart"></el-button>
              <!--  -->
                <el-button class="blue_btn" @click="saveCourse"></el-button>
              </div>
            </el-col>
          </el-row>
          <el-table
            :data="showCourse"
            height="400"
            border
            style="width: 100%">
            <el-table-column label="" align="center">
              <template slot-scope="scope">
                {{scope.row.title}} <br>
                ({{scope.row.shortcode}})
              </template>
            </el-table-column>
            <el-table-column prop="credits" label="" align="center" width="60"></el-table-column>
            <el-table-column label="/" align="center" width="60">
              <template slot-scope="scope">
                <div v-if="scope.row.coursetype==1"></div>
                <div v-else></div>
              </template>
            </el-table-column>
            <el-table-column prop="longcode" label="" align="center" width="120"></el-table-column>
            <el-table-column label="" align="center" width="60">
              <template slot-scope="scope">
                <el-button type="text" @click="lookOneCourse(scope.row.shortcode)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-card>
      </el-col>
      <!--  -->
      <el-col :span="14">
        <el-card style="margin-left:20px;">
          <div v-if="lookCourse.length>0" style="font-size:18px; font-weight:bold; margin-bottom:20px;">
            {{lookCourse[0].title}} {{lookCourse[0].coursecode.substr(0,lookCourse[0].coursecode.length-2)}}
          </div>
          <div v-else style="font-size:18px; font-weight:bold; margin-bottom:20px;"></div>
          <el-table
            :data="lookCourse"
            height="400"
            border
            style="width: 100%">
            <el-table-column label="" align="center" width="100">
              <template slot-scope="scope">
                <div v-if="scope.row.coursecode==lookCourseGreen" style="display:flex; justify-content:center;"><el-button @click="alreadyOwned" type="success" size="medium" style="font-weight:bold;font-size:15px;">{{scope.row.coursecode}}</el-button></div>
                <div v-else style="display:flex; justify-content:center;"><el-button @click="trySelect(scope.row)" type="success" plain size="medium" style="font-weight:bold;font-size:15px;">{{scope.row.coursecode}}</el-button></div>
              </template>
            </el-table-column>
            <el-table-column prop="teacher.fullname" label="" align="center" width="100"></el-table-column>
            <el-table-column label="" align="center" width="100">
              <template slot-scope="scope">
                <div v-if="scope.row.campus==1" style="background:#F7DB78;"></div>
                <div v-else-if="scope.row.campus==2" style="background:#F7E2DB;"></div>
                <div v-else-if="scope.row.campus==3" style="background:#ECEDEB;"></div>
                <div v-else style="background:#DCDFE6;"></div>
              </template>
            </el-table-column>
            <el-table-column label="/" align="center" width="100">
              <template slot-scope="scope">
                <div>
                  {{scope.row.selectednumber}} / {{scope.row.capacity}}
                </div>
              </template>
            </el-table-column>
            <el-table-column prop="info" label="" align="center"></el-table-column>
            <el-table-column prop="note" label="" align="center" width="100"></el-table-column>
          </el-table>
        </el-card>
      </el-col>
    </el-row>
    <!--  -->
    <br>
    <el-card :key="scheduleKey">
      <ScheduleTable :schedule="schedule" />
    </el-card>
  </div>
</template>

<script>
import ScheduleTable from './ScheduleTable';
export default {
    name:"SelectCourseInfo",
    components:{
      ScheduleTable,
    },
    data() {
      return {
        // 
        userInfo:{},
        college:{},
        // 
        ownedCourse:[],
        // 
        showCourse:[],
        // 
        lookCourse:[],
        lookCourseGreen:"",
        // 
        scheduleCourse:[],
        schedule:{},
        scheduleKey:0,
      }
    },
    methods: {
      // 
      reloadOwnedCourse(){
        this.$api.userInfo.getUserInfo(sessionStorage.getItem("username"))
      .then(res=>{
          // 
          this.userInfo = res.data["data"]["user"];
          this.ownedCourse = res.data["data"]["user"]["courses"];
        })
      },

      // 
      saveCourse(){
        // 
        this.scheduleKey++;
        this.loadSchedule();

        const h = this.$createElement;
        this.$notify({
          title: '',
          message: h('i', { style: 'color: teal'}, ''),
          position: 'top-left',
          offset: 150,
        });
      },
      // 
      addCourseCart(){
        this.$api.course.getMajorCourses(this.userInfo.major.majorcode,this.userInfo.username)
        .then(res=>{
          this.showCourse = res.data["data"]["courses"];
          const h = this.$createElement;
          this.$notify({
            title: '',
            message: h('i', { style: 'color: teal'}, ''),
            position: 'top-left',
            offset: 150
          });
        })
      },

      // 
      lookOneCourse(shortcode){
        this.$api.course.getShortCodeCourse(shortcode)
        .then(res=>{
          this.lookCourse = res.data["data"]["courses"];
          this.showCourse.forEach(item =>{
            if(shortcode==item["shortcode"]){
              this.lookCourseGreen = item["longcode"];
            }
          })
        })
      },
      // 
      alreadyOwned(){
        // this.$notify({
        //   title: '',
        //   message: '',
        //   type: 'info',
        //   offset:150
        // });
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
          center: true
        }).then(() => {
          this.$api.course.withdrawalFromClass(sessionStorage.getItem("username"),this.lookCourseGreen);
          // 
          this.scheduleKey++;
          this.loadSchedule();
          this.reloadOwnedCourse();
          this.$notify({
            title: '',
            message: '',
            type: 'success',
            offset:150
          });
          this.showCourse.forEach(item =>{
            if(item.shortcode==this.lookCourseGreen.slice(0,this.lookCourseGreen.length-2)){
              item.longcode = "";
            }
          })
          this.lookCourseGreen = "";
        }).catch(() => {
          this.$notify({
            title: '',
            message: '',
            type: 'info',
            offset:150
          });
        });
      },
      // 
      trySelect(tryCourse){
        if(this.checkconflict(tryCourse)){
          if(this.lookCourseGreen!=""){
            this.$api.course.withdrawalFromClass(sessionStorage.getItem("username"),this.lookCourseGreen);
          }
          this.$api.course.selectCourse(sessionStorage.getItem("username"),tryCourse.coursecode)
          .then(()=>{
            this.lookCourseGreen = tryCourse.coursecode;
            this.showCourse.forEach(item =>{
              if(item.shortcode==this.lookCourseGreen.slice(0,this.lookCourseGreen.length-2)){
                item.longcode = tryCourse.coursecode;
              }
            })
            // 
            this.scheduleKey++;
            this.loadSchedule();
            this.reloadOwnedCourse();
            this.$notify({
              title: '',
              message: '',
              type: 'success',
              offset:150
            });
          })
          .catch(err=>{
            this.$notify({
              title: '',
              message: err.response.data["msg"],
              type: 'error',
              offset:150
            });
          })
        }
        else{
          console.log("");
          this.$notify({
            title: '',
            message: '',
            type: 'error',
            offset:150
          });
        }
      },
      // 
      checkconflict(tryCourse){
        // 
        this.reloadOwnedCourse();
        // console.log(this.ownedCourse);
        let owned_odd_all = []
        let owned_even_all = []
        this.ownedCourse.forEach(course=>{
          let owneddict = this.$api.course.readCourse(course);
          owned_odd_all = [...owned_odd_all,...owneddict.classtime_odd_all];
          owned_even_all = [...owned_even_all,...owneddict.classtime_even_all];
        })
        // console.log(owned_odd_all,owned_even_all);
        // console.log(owned_even_all.includes("103"));
        // console.log(tryCourse);
        let trydict = this.$api.course.readCourse(tryCourse);
        let flag = true;
        trydict.classtime_odd_all.forEach(t=>{
          if(owned_odd_all.includes(t)){
            flag = false;
          }
          // console.log(owned_odd_all.includes(t));
        });
        trydict.classtime_even_all.forEach(t=>{
          if(owned_even_all.includes(t)){
            flag = false;
          }
        });
        // console.log(flag);
        return flag;
      },


      // 
      closeSelect(){
        this.$router.push("/student/selectCourse");
        this.$message({
          message:"",
          type:"success"
        })
      },

      //
      // 
      reloadSchedule(){
        this.loadSchedule();
        this.scheduleKey++;
      },
      // 
      loadSchedule(){
        this.$api.userInfo.getUserInfo(sessionStorage.getItem("username"))
        .then(res=>{
          this.scheduleCourse = res.data['data']['user']['courses'];
          this.schedule = this.$api.course.readSchedule(this.scheduleCourse);
        })
        .catch(err=>{
          this.$message({
            message:""+err.response.data["msg"],
            type:"error"
          })
        });
      },
    },
    created(){
      this.loadSchedule();
    },
    mounted() {
      this.$api.userInfo.getUserInfo(sessionStorage.getItem("username"))
      .then(res=>{
        // 
        this.userInfo = res.data["data"]["user"];
        this.ownedCourse = res.data["data"]["user"]["courses"];
        // 
        this.$api.userInfo.getCollege(this.userInfo.major.pcode)
        .then(res=>{
          this.college = res.data["data"]["college"];
        })
        .catch(()=>{
          this.college = this.userInfo.major;
        });
      })
    }
}
</script>

