<template>
  <div>
    <div class="nav_title"></div>
      <div v-if="stage!=2" class="noavailable">
        <el-button @click="reload"></el-button>
      </div>
      <div v-else>
        <el-card>
          <div>
            <div style="font-weight:bold;font-size:20px;">{{year}}-{{year+1}}{{currentTerm}} </div>
            <el-row>
              <el-col :span="20">
                <p>{{today}} </p>
                <p>{{today}} </p>
                <p> |  |  | |  |  |  |  |  |  |  | 2018 | </p>
                <p>1.2.</p>
              </el-col>
              <el-col :span="4">
                <el-button class="blue_btn" style="margin-top:30px;width:120px;height:45px;" @click="dialogVisible = true"></el-button>
                <el-dialog
                  title=""
                  :visible.sync="dialogVisible"
                  width="60%">
                  <h3 style="display:flex;justify-content:center;"></h3>
                  <div style="background-color:#DCDFE6;padding:10px;">41</div>
                  <span slot="footer" class="dialog-footer" style="display:flex;justify-content:center;">
                    <el-button class="blue_btn" @click="openSelect"> </el-button>
                  </span>
                </el-dialog>
              </el-col>
            </el-row>
          </div>
        </el-card>
      </div>
  </div>
</template>

<script>
export default {
    name:"SelectCourse",
    data() {
      return {
        currentTerm:0,
        stage:1,
        year:0,
        today:"",
        dialogVisible:false,

      }
    },
    methods: {
      // 
      openSelect(){
        this.dialogVisible = false;
        const loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        setTimeout(()=>{
          loading.close();
          this.$message({
            message:"",
            type:"success"
          })
        },1000);
        // 
        this.$router.push('/student/selectCourseInfo');
      },
      // 
      reload(){
        const loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        // setTimeout(()=>{
        //   loading.close();
        //   this.$api.userInfo.getUserInfo(sessionStorage.getItem("username"))
        //   .then(res=>{
        //     this.currentTerm = res.data["data"]["user"]["term"];
        //     this.stage = res.data["data"]["user"]["stage"];
        //     this.$message({
        //       message:"",
        //       type:"success",
        //     });
        //   })
        //   .catch(err=>{
        //     loading.close();
        //     this.$message({
        //       message:err.response.data["msg"],
        //       type:"error",
        //     });
        //   })
        // },1000);
        this.$api.userInfo.getUserInfo(sessionStorage.getItem("username"))
        .then(res=>{
          this.currentTerm = res.data["data"]["user"]["term"];
          this.stage = res.data["data"]["user"]["stage"];
          loading.close();
          this.$message({
            message:"",
            type:"success",
          });
        })
        .catch(err=>{
          loading.close();
          this.$message({
            message:err.response.data["msg"],
            type:"error",
          });
        })
      }
    },
    mounted(){
      this.year = new Date().getFullYear();
      this.today = new Date().toISOString();
    }
}
</script>

<style scoped>
  .noavailable{
    display: flex;
    justify-content: center;
    color: #909399;
    font-size: 25px;
  }
</style>