<template>
  <div>
    <!--  -->
    <el-card>
    <div class="nav_title" style="display:flex; justify-content:center;">
      <el-button @click="renext"></el-button>
      <div style="margin:5px 20px;"></div>
      <el-button @click="next"></el-button>
    </div>
    <br> <br>
    <div>
      <el-steps :active="active" simple finish-status="success">
        <el-step title="" icon="el-icon-search"></el-step>
        <el-step title="" icon="el-icon-upload"></el-step>
        <el-step title="" icon="el-icon-edit"></el-step>
      </el-steps>
      <el-progress :percentage="active/3*100" status="success"></el-progress>
    </div>
    </el-card>
    <br>
    <!--  -->
    <el-card>
      <template v-if="userInfo.stage!=5">
        <div style="text-align:center;">
          <div class="nav_title" style="font-size:30px; margin:30px 0;"></div>
          <div class="spantitle"><br> <br>  </div>
          <br> <br>
        </div>
      </template>
      <!--  -->
      <template v-if="userInfo.stage==5 && active==0">
        <div>
          <el-table
          :data="courses"
          :default-sort="{prop:'term',order:'descending'}">
            <el-table-column prop="term" label="" align="center" width="120" sortable></el-table-column>
            <el-table-column prop="coursecode" label="" align="center"></el-table-column>
            <el-table-column prop="title" label="" align="center"></el-table-column>
            <el-table-column label="" align="center">
              <template slot-scope="scope">
                <div style="display:none;">{{scope.row.title}}</div>
                <el-button type="primary" @click="clickCourse(scope.row.coursecode)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </template>
      <!--  -->
      <template v-if="userInfo.stage==5 && active==1">
        <div>
          <div style="margin:20px 40px 0;" class="spantitle">{{activeCourse}}</div>
        </div>
        <br>
        <div>
          <el-table
          :data="students"
          :default-sort="{prop:'term',order:'descending'}">
            <el-table-column prop="username" label="" align="center" width="120" sortable></el-table-column>
            <el-table-column prop="fullname" label="" align="center"></el-table-column>
            <el-table-column prop="major.title" label="" align="center"></el-table-column>
            <el-table-column label="" align="center">
              <template slot-scope="scope">
                <div style="display:none;">{{scope.row.username}}</div>
                <el-button type="primary" @click="clickStudent(scope.row.username)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </template>
      <!--  -->
      <template v-if="userInfo.stage==5 && active==2">
      <div style="text-align:center;">
        <div>
          <el-row style="margin:20px 0;" class="spantitle">
            <el-col :span="4"><div>{{activeCourse}}</div></el-col>
            <el-col :span="4"><div>{{activeStudent}}</div></el-col>
          </el-row>
        </div>
        <br>
        <div>
          <el-row style="margin:20px 0;" class="spantitle">
            <el-col :span="4" :offset="4"><div></div></el-col>
            <el-col :span="4"><div>1</div></el-col>
            <el-col :span="4"><div>2</div></el-col>
            <el-col :span="4"><div></div></el-col>
          </el-row>
        </div>
        <div>
          <el-form :inline="true" :model="percentage" class="demo-form-inline">
            <el-form-item label="" style="font-weight:bold;"></el-form-item>
            <el-form-item>
              <el-input v-model="percentage.score1" placeholder="(1 - 100)"></el-input>
            </el-form-item>
            <el-form-item>
              <el-input v-model="percentage.score2" placeholder="1(1 - 100)"></el-input>
            </el-form-item>
            <el-form-item>
              <el-input v-model="percentage.score3" placeholder="2(1 - 100)"></el-input>
            </el-form-item>
            <el-form-item>
              <el-input v-model="percentage.score4" placeholder="(1 - 100)"></el-input>
            </el-form-item>
          </el-form>
        </div>
        <div>
          <el-form :inline="true" :model="score" class="demo-form-inline">
            <el-form-item label="" style="font-weight:bold;">
            </el-form-item>
            <el-form-item>
              <el-input v-model="score.score1" placeholder="(1 - 100)"></el-input>
            </el-form-item>
            <el-form-item>
              <el-input v-model="score.score2" placeholder="1(1 - 100)"></el-input>
            </el-form-item>
            <el-form-item>
              <el-input v-model="score.score3" placeholder="2(1 - 100)"></el-input>
            </el-form-item>
            <el-form-item>
              <el-input v-model="score.score4" placeholder="(1 - 100)"></el-input>
            </el-form-item>
          </el-form>
        </div>
        <div class="spantitle" style="margin:20px 0 20px 500px; text-align:start; "><span style="margin-left:10px;">{{gradepoint1}}</span></div>
        <div class="spantitle" style="margin:20px 0 20px 500px; text-align:start; "><span style="margin-left:10px;">{{gradepoint2}}</span></div>
        <el-rate
          v-model="gradepoint2"
          disabled
          show-score
          text-color="#ff9900"
          score-template="{value}">
        </el-rate>
        <br>
        <el-button type="primary" @click="submitGrade"></el-button>
        <br> <br> <br>
        <div style="text-align:start; margin:50px;">
          <span class="spantitle"></span>
          <p>1</p>
          <p>212</p>
          <p>3100%</p>
          <p>40-100</p>
        </div>
        <br>
      </div>
      </template>
      <!--  -->
      <template v-if="userInfo.stage==5 && active==3">
        <div style="text-align:center;">
          <div class="nav_title" style="font-size:30px; margin:30px 0;"></div>
          <div class="spantitle"><br> </div>
          <br> <br>
        <el-button type="primary" @click="next"></el-button>
        </div>
      </template>
    </el-card>
  </div>
</template>

<script>
export default {
    name:"WriteGrades",
    data() {
      return {
        // 
        userInfo:{},
        courses:[],
        // 
        active:0,
        // 
        activeCourse:"",
        students:[],
        activeStudent:"",
        score:{"score1":0,"score2":0,"score3":0,"score4":0},
        percentage:{"score1":10,"score2":20,"score3":30,"score4":40}
      }
    },
    computed:{
      gradepoint1(){
        let allscore = 0.01*(this.score.score1*this.percentage.score1+this.score.score2*this.percentage.score2+this.score.score3*this.percentage.score3+this.score.score4*this.percentage.score4);
        return allscore;
      },
      gradepoint2(){
        let allscore = 0.01*(this.score.score1*this.percentage.score1+this.score.score2*this.percentage.score2+this.score.score3*this.percentage.score3+this.score.score4*this.percentage.score4);
        if(allscore>=90){
          return 5
        }
        else if(allscore>=80){
          return 4
        }
        else if(allscore>=70){
          return 3
        }
        else if(allscore>=60){
          return 2
        }
        else{
          return 0
        }
      }
    },
    methods:{
      // 
      next() {
        if (this.active++ > 2) this.active = 0;
      },
      renext(){
        if (this.active-- < 1 ) this.active = 3;
      },

      // 1.
      clickCourse(code){
        this.activeCourse=code;
        // 
        this.$api.course.getCourseStudents(this.activeCourse)
        .then(res=>{
          this.students = res.data["data"]["students"];
        })
        this.next();
      },

      // 2.
      clickStudent(username){
        this.activeStudent = username;
        // 
        this.$api.grades.tryGetGrade(this.activeCourse,this.activeStudent)
        .then(res=>{
          if(res.data["code"]==200){
            this.score.score1 = res.data["data"]["grade"]['score1'];
            this.score.score2 = res.data["data"]["grade"]['score2'];
            this.score.score3 = res.data["data"]["grade"]['score3'];
            this.score.score4 = res.data["data"]["grade"]['score4'];
          }
        })
        this.next();
      },

      // 3.
      submitGrade(){
        let data = {
          "term":this.userInfo.term,
          "cid":this.activeCourse,
          "sid":this.activeStudent,
          "score1":this.score.score1,
          "score2":this.score.score2,
          "score3":this.score.score3,
          "score4":this.score.score4,
          "gradepoint":this.gradepoint2,
        }
        this.$api.grades.writeGrade(data)
        .then(()=>{
          this.$notify({
            title: '',
            message: '',
            type: 'success',
            offset:150
          });
          this.next();
        })
        .catch(()=>{
          this.$notify({
            title: '',
            message: '',
            type: 'error',
            offset:150
          });
        })
      },

      // 
      getMyTeach(){
        this.$api.course.getTeach(this.userInfo.username)
        .then(res=>{
          this.courses = res.data["data"]['courses'];
        })
      }
    },
    created(){
      this.$api.userInfo.getUserInfo(sessionStorage.getItem("username"))
      .then(res=>{
        this.userInfo = res.data["data"]["user"];
        this.getMyTeach();
      })
    }
}
</script>

<style scoped>

</style>