<template>
  <div>
    <!--  -->
    <div class="nav_title"> <el-button type="text" @click="reloadSchedule"></el-button></div> 
    <el-card :key="scheduleKey">
      <ScheduleTable :schedule="schedule"/>
    </el-card>
    <!--  -->
    <br> <br>
    <div class="nav_title"></div>
    <el-card>
      <el-table stripe :data="scheduleCourse">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="coursecode" label="" align="center" width="100"></el-table-column>
        <el-table-column prop="title" label="" align="center" width="200"></el-table-column>
        <el-table-column prop="teacher.major.title" label="" align="center" width="150"></el-table-column>
        <el-table-column prop="teacher.fullname" label="" align="center" width="100"></el-table-column>
        <el-table-column prop="credits" label="" align="center" width="80"></el-table-column>
        <el-table-column label="" align="center" width="120">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.campus==1" type="success"></el-tag>
            <el-tag v-else-if="scope.row.campus==2"></el-tag>
            <el-tag v-else-if="scope.row.campus==0" type="warning"></el-tag>
            <el-tag v-else-if="scope.row.campus==3" type="info"></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="info" label="" align="center"></el-table-column>
        <el-table-column label="" align="center" width="80">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.coursetype==1"></el-tag>
            <el-tag v-else-if="scope.row.coursetype==2" type="success"></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="capacity" label="" align="center" width="80"></el-table-column>
        <el-table-column prop="selectednumber" label="" align="center" width="80"></el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
import ScheduleTable from './ScheduleTable';
export default {
    name:"MySchedule",
    components:{
      ScheduleTable,
    },
    data() {
      return {
        scheduleCourse:[],
        schedule:{},
        scheduleKey:0,
      }
    },
    methods:{
      // 
      reloadSchedule(){
        this.loadSchedule();
        this.scheduleKey++;
        const h = this.$createElement;
        this.$notify({
          title: '',
          message: h('i', { style: 'color: teal'}, ''),
          position: 'top-left',
          offset:150
        });
      },
      // 
      loadSchedule(){
        this.$api.userInfo.getUserInfo(sessionStorage.getItem("username"))
        .then(res=>{
          this.scheduleCourse = res.data['data']['user']['courses'];
          this.schedule = this.$api.course.readSchedule(this.scheduleCourse);
        })
        .catch(err=>{
          this.$message({
            message:""+err.response.data["msg"],
            type:"error"
          })
        });
      },
    },
    created(){
      this.loadSchedule();
    },
    mounted(){

    },
    activated(){
      this.loadSchedule();
      this.scheduleKey++;
    }
    
}
</script>

<style scoped>

</style>