// 
// 
export function readCourse(course){
    // 
    let dict = {};
    // 
    dict["coursecode"] = course.coursecode;
    // 
    dict["title"] = course.title;
    // 
    dict["tearcher"] = course.teacher.fullname;
    // 
    dict["classroom"] = course.classroom;
    // 
    let classtime_odd = course.classtime.split(",")[0].split("+");
    let classtime_even = course.classtime.split(",")[1].split("+");
    dict["classtime_odd"] = classtime_odd;
    dict["classtime_even"] = classtime_even;
    // 
    let classtime_odd_all = []
    classtime_odd.forEach(prd =>{
        let newprd = prd.split("-");
        let newprd_all = [];
        if(newprd.length<=1){
            newprd_all = newprd;
        }
        else{
            for(let i=parseInt(newprd[0]); i<=parseInt(newprd[1]); i++){
                newprd_all.push(i.toString());
            }
        }
        classtime_odd_all = [...classtime_odd_all,...newprd_all];
    });
    let classtime_even_all = []
    classtime_odd.forEach(prd =>{
        let newprd = prd.split("-");
        let newprd_all = [];
        if(newprd.length<=1){
            newprd_all = newprd;
        }
        else{
            for(let i=parseInt(newprd[0]); i<=parseInt(newprd[1]); i++){
                newprd_all.push(i.toString());
            }
        }
        classtime_even_all = [...classtime_even_all,...newprd_all];
    });
    dict["classtime_odd_all"] = classtime_odd_all;
    dict["classtime_even_all"] = classtime_even_all;
    return dict;
}

//  :
export function readOwnedCourse(ownedCourse){
    let ownedCourseTime_odd = {};
    let ownedCourseTime_even = {};
    ownedCourse.forEach(course=>{
        let dict = readCourse(course);
        dict.classtime_odd_all.forEach(aprd=>{
            ownedCourseTime_odd[aprd] = course.title;
        });
        dict.classtime_even_all.forEach(aprd=>{
            ownedCourseTime_even[aprd] = course.title;
        });
    });

    return {
        "odd":ownedCourseTime_odd,
        "even":ownedCourseTime_even
    }
}


// API
export function readSchedule(scheduleCourse){
    let schedule = {};
    scheduleCourse.forEach(course => {
        let dict = readCourse(course);
        // console.log(dict);
        // 
        let long = dict["classtime_odd"].length>=dict["classtime_even"].length?"classtime_odd":"classtime_even";
        let short = dict["classtime_odd"].length<dict["classtime_even"].length?"classtime_odd":"classtime_even";
        let a_classtime_long = dict[long];
        let a_classtime_short = dict[short];
        a_classtime_long.forEach(prd =>{
            // 
            let course_schedule = {};
            course_schedule["title"] = course.title;
            course_schedule["teacher"] = course.teacher.fullname;
            course_schedule["classroom"] = course.classroom;
            let prd_ct = prd.split("-");
            if(prd_ct.length === 1){
                course_schedule["continue"] = 1;
            }
            else{
                course_schedule["continue"] = parseInt(prd_ct[1])-parseInt(prd_ct[0]) + 1;
            }
            if(a_classtime_short.includes(prd)){
                course_schedule["note"] = "";
            }
            else{
                course_schedule["note"] = long=="classtime_odd"?"":"";
            }
            // 
            schedule[prd.split("-")[0]] = course_schedule;
        });
    });
    // console.log(schedule);
    return schedule;
}

// 
export function renderSchedule(schedule){
    Object.entries(schedule).forEach(([key,val])=>{
    //   console.log(key,val);
      let element = document.getElementById(key);
      element.setAttribute("rowspan",val.continue);
      for(let i=1; i<val.continue; i++){
        // console.log(i);
        let deleteElement = document.getElementById((parseInt(key)+i).toString());
        deleteElement.remove();
      }
      let text = val.title + "<br/>" + val.teacher + "<br/>" + val.classroom;
      text += val.note==""?"":val.note;
      // all.cssscopedshiy
      element.innerHTML = '<div class="purple_class">'+ text +'</div>';
    })
  }