<template>
  <div>
    <el-card>
    <div class="nav_title"></div>
      <el-steps :active="userInfo.stage-1" finish-status="success" simple style="margin-top: 20px">
        <el-step title="" ></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>
    </el-card>
    <br> <br>
    <el-card>
    <div class="nav_title" style="text-align:center;"></div>
      <el-table
      :data="grades"
      border
      :default-sort="{prop:'term',order:'ascending'}">
      <el-table-column prop="term" label="" align="center" sortable  width="120"></el-table-column>
      <el-table-column prop="cid.coursecode" label="" align="center" width="140"></el-table-column>
      <el-table-column prop="cid.title" label="" align="center" show-overflow-tooltip></el-table-column>
      <el-table-column prop="cid.credits" label="" align="center" width="100"></el-table-column>
      <el-table-column label="" align="center" width="80">
        <template slot-scope="scope">
          <div v-if="scope.row.cid.coursetype==1"></div>
          <div v-else-if="scope.row.cid.coursetype==2"></div>
          <div v-else></div>
        </template>
      </el-table-column>
      <el-table-column prop="score1" label="" align="center" width="80"></el-table-column>
      <el-table-column prop="score2" label="1" align="center" width="80"></el-table-column>
      <el-table-column prop="score3" label="2" align="center" width="80"></el-table-column>
      <el-table-column prop="score4" label="" align="center" width="80"></el-table-column>
      <el-table-column prop="" label="" align="center" width="100">
        <template slot-scope="scope">
          <div v-if="scope.row.gradepoint==0"></div>
          <div v-else-if="scope.row.gradepoint==2"></div>
          <div v-else-if="scope.row.gradepoint==3"></div>
          <div v-else-if="scope.row.gradepoint==4"></div>
          <div v-else-if="scope.row.gradepoint==5"></div>
          <div v-else> </div>
        </template>
      </el-table-column>
      <el-table-column prop="gradepoint" label="" align="center" width="100"></el-table-column>
      <el-table-column prop="updated_at" label="" align="center" width="100" show-overflow-tooltip></el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
    name:"CheckGrades",
    data() {
      return {
        userInfo:{},
        grades:[],
      }
    },
    methods:{
      getMyGrades(){
        this.$api.grades.getGrades(this.userInfo.username)
        .then(res=>{
          this.grades = res.data["data"]["grades"];
        })
      }
    },
    created(){
      this.$api.userInfo.getUserInfo(sessionStorage.getItem("username"))
      .then(res=>{
        this.userInfo = res.data["data"]["user"];
        this.getMyGrades();
      });
    }
}
</script>

<style scoped>
  .spantitle{
    font-size:16px;
    color:#303133;
    font-weight: bold;
  }
</style>