<template>
  <div class="LoginPage_all">
    <div class="login_container">
        <div class="logintitle"></div>
        <el-form :model="loginForm" :rules="rules" ref="loginFormRef" label-width="80px" class="loginform_all">
            <el-form-item label="" prop="account">
                <el-input placeholder="" v-model="loginForm.account" prefix-icon="el-icon-star-off"></el-input>
            </el-form-item>
            <el-form-item label="" prop="password">
                <el-input placeholder="" v-model="loginForm.password" :type="isShow?'text':'password'" prefix-icon="el-icon-view"></el-input>
            </el-form-item>
            <el-switch v-model="isShow" active-text="" class="switch"></el-switch>
            <el-form-item class="btns">
                <el-button type="primary" @click="login" class="btn" icon="el-icon-circle-check"></el-button>
                <!-- <el-button type="info" class="btn" @click="signUp" icon="el-icon-edit"></el-button> -->
            </el-form-item>
        </el-form>
    </div>
  </div>
</template>

<script>
export default {
    name:"LoginPage",
    data() {
        return {
            // inputtype
            isShow:false,
            // 
            loginForm:{
                account:"",
                password:"",
            },
            // 
            rules: {
                account: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 4, max: 7, message: ' 4  7 ', trigger: 'blur' }
                ],
                password: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 5, max: 30, message: ' 5  30 ', trigger: 'blur' }
                ],
            }
        }
    },
    methods: {
        login(){
            // refs
            this.$refs.loginFormRef.validate(valid=>{
                // 
                if(!valid) return;
                console.log(this.$api)
                this.$api.userInfo.login({
                    username:this.loginForm.account,
                    password:this.loginForm.password
                })
                .then((res) => {
                    // console.log('',res.data)
                    this.$message({
                    message: '',
                    type: 'success'
                    });
                    // sessionStorage
                    // console.log(res.data.data["aams-token"])
                    sessionStorage.setItem("aams-token",res.data["data"]["aams-token"]);
                    sessionStorage.setItem("username",this.loginForm.account);
                    sessionStorage.setItem("identity",res.data['data']['identity']);
                    // 
                    this.$router.push('/home');
                })
                .catch(()=>{
                    // console.log('',error.message);
                    this.$message.error('');
                })
                
            })
            
        },
        // signUp(){
        //     this.$message.error('...');
        // }
    },
}
</script>

<style scoped>
    .LoginPage_all{
        height: 100%;
        background-image: url('../assets/img/TONGJI1028.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .login_container{
        background-color: #F2F6FC;
        width: 500px;
        height: 380px;
        border-radius: 10px;
        flex-direction: column;
        box-shadow: 0 0 8px #ddd;
        position: absolute;
    }
    .logintitle{
        padding-top: 40px;
        padding-bottom: 10px;
        font-size: 25px;
        text-align: center;
    }
    .loginform_all{
        padding-top: 30px;
        padding-left: 40px;
        padding-right: 80px;
    }
    .switch{
        position: absolute;
        right: 80px;
    }
    .btns{
        margin-top: 70px;
        display: flex;
        flex-direction: row;
        justify-content: center;
    }
    .btn{
        /* width: 100px; */
        width: 300px;
        margin-right: 25px;
    }
</style>